import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import InitialScreen from './InitialScreen';
import QuestionScreen from './QuestionScreen';
import ResultScreen from './ResultScreen';
import DifyResultScreen from './DifyResultScreen';
import { calculateZodiacSign, getMBTIType, getTypeDescription } from '../utils/helpers';

const MBTITest = () => {
  // 
  const [step, setStep] = useState(1); // 1: , 2: , 3: , 4: Dify
  const [birthdate, setBirthdate] = useState({
    year: new Date().getFullYear() - 20,
    month: 1,
    day: 1
  });
  const [gender, setGender] = useState('');
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [scores, setScores] = useState({
    E: 0, I: 0,
    S: 0, N: 0,
    T: 0, F: 0,
    J: 0, P: 0
  });
  const [mbtiType, setMbtiType] = useState('');
  const [zodiacSign, setZodiacSign] = useState('');
  const [loading, setLoading] = useState(false);
  const [difyResponse, setDifyResponse] = useState(null);
  const navigate = useNavigate();

  // MBTI
  const questions = [
    {
      text: "",
      dimension: "EI",
      direction: "E"
    },
    {
      text: "",
      dimension: "SN",
      direction: "N"
    },
    {
      text: "",
      dimension: "TF",
      direction: "F"
    },
    {
      text: "",
      dimension: "JP",
      direction: "P"
    },
    {
      text: "",
      dimension: "EI",
      direction: "I"
    },
    {
      text: "",
      dimension: "SN",
      direction: "N"
    },
    {
      text: "",
      dimension: "TF",
      direction: "F"
    },
    {
      text: "",
      dimension: "JP",
      direction: "J"
    },
    {
      text: "",
      dimension: "EI",
      direction: "E"
    },
    {
      text: "",
      dimension: "SN",
      direction: "N"
    }
  ];

  // 
  const handleStartDiagnosis = () => {
    if (!gender) {
      alert('');
      return;
    }

    const calculatedZodiac = calculateZodiacSign(birthdate.month, birthdate.day);
    setZodiacSign(calculatedZodiac);
    setStep(2);
  };

  // 
  const handleAnswer = (score) => {
    const question = questions[currentQuestion];
    const { dimension, direction } = question;

    const newScores = { ...scores };
    if (direction === dimension[0]) {
      newScores[dimension[0]] += score;
    } else {
      newScores[dimension[1]] += score;
    }

    setScores(newScores);

    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
    } else {
      // 
      const type = getMBTIType(newScores);
      setMbtiType(type);
      setStep(3);
    }
  };

  // Dify
  const sendToDify = async () => {
    // 
    setDifyResponse(null);
    setLoading(true);

    // 
    const testResponseData = {
      "": "ENFP",
      "": "",
      "": "ENFP",
      "": ""
    };

    // API
    const USE_TEST_DATA = false;

    if (USE_TEST_DATA) {
      console.log(':', testResponseData);
      // 
      const isValid = hasValidContent(testResponseData);
      console.log(':', isValid);

      if (isValid) {
        setDifyResponse(testResponseData);
        setLoading(false);
        setStep(4);
        return;
      } else {
        console.error('');
      }
    }

    // ID
    const sessionId = `${new Date().getTime()}-${Math.random().toString(36).substring(2, 9)}`;
    console.log(`Dify API  (ID: ${sessionId})`);

    try {
      // AbortController
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 45000); // 45

      // API URL
      const apiUrl = process.env.REACT_APP_DIFY_API_URL || 'https://api.dify.ai/v1/chat-messages';
      console.log('API URL:', apiUrl);

      const apiKey = process.env.REACT_APP_DIFY_API_KEY || 'app-sIkEDjFqaRrWwmbZAkIIwfZO';
      console.log('API (10):', apiKey.substring(0, 10) + '...');

      // 
      const requestBody = {
        inputs: {
          zodiac: zodiacSign,
          mbti: mbtiType,
          gender: gender
        },
        query: "MBTI",
        response_mode: "blocking",
        user: sessionId
      };

      // APIworkflows/run
      if (apiUrl.includes('/workflows/run')) {
        requestBody.inputs = {
          zodiac: zodiacSign,
          mbti: mbtiType,
          gender: gender
        };
        // workflows API
        delete requestBody.query;
        delete requestBody.response_mode;
      }

      console.log(':', JSON.stringify(requestBody));

      const response = await fetch(apiUrl, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${apiKey}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(requestBody),
        signal: controller.signal
      });

      // 
      clearTimeout(timeoutId);

      if (!response.ok) {
        const errorText = await response.text();
        console.error(`Dify API  (${response.status}): ${errorText}`);
        throw new Error(`API (${response.status}): `);
      }

      const data = await response.json();
      console.log('Dify API response:', data);
      console.log('Dify API response:', typeof data);
      console.log('Dify API response:', Object.keys(data));

      //  - DifyResultScreen
      console.log('');
      setDifyResponse(data);
      setLoading(false);
      setStep(4);
    } catch (error) {
      console.error('Dify API :', error);
      setLoading(false);
      alert(`: ${error.message}`);
      // 
      setStep(3);
    }
  };

  // 
  const hasValidContent = (data) => {
    // 
    if (!data || typeof data !== 'object') {
      console.log('hasValidContent: ');
      return false;
    }

    // 1OK
    const categories = ['', '', '', ''];

    // 
    const validCategories = categories.filter(category => {
      const content = data[category];
      const isValid = content &&
                      typeof content === 'string' &&
                      content.trim().length > 5; // 5

      console.log(`${category}: ${isValid ? '' : ''} (${content ? content.length : 0})`);
      return isValid;
    });

    // 1
    const isValid = validCategories.length > 0;
    console.log(`: ${validCategories.length}`);
    return isValid;
  };

  // 
  const resetTest = () => {
    // 
    setStep(1);
    setCurrentQuestion(0);
    setScores({
      E: 0, I: 0,
      S: 0, N: 0,
      T: 0, F: 0,
      J: 0, P: 0
    });
    setMbtiType('');
    setZodiacSign('');
    setLoading(false);
    setDifyResponse(null);
    setBirthdate({
      year: new Date().getFullYear() - 20,
      month: 1,
      day: 1
    });
    setGender('');
  };

  // 
  const goToResults = () => {
    if (difyResponse) {
      navigate('/result', {
        state: {
          difyResponse,
          mbtiType,
          zodiacSign,
          gender
        }
      });
    }
  };

  // step 4
  useEffect(() => {
    if (step === 4 && difyResponse) {
      goToResults();
    }
  }, [step, difyResponse]);

  // 
  const renderStep = () => {
    switch(step) {
      case 1:
        return (
          <InitialScreen
            birthdate={birthdate}
            setBirthdate={setBirthdate}
            gender={gender}
            setGender={setGender}
            onStartDiagnosis={handleStartDiagnosis}
          />
        );

      case 2:
        return (
          <QuestionScreen
            currentQuestion={currentQuestion}
            totalQuestions={questions.length}
            question={questions[currentQuestion]}
            onAnswer={handleAnswer}
          />
        );

      case 3:
        return (
          <ResultScreen
            zodiacSign={zodiacSign}
            mbtiType={mbtiType}
            typeDescription={getTypeDescription(mbtiType)}
            scores={scores}
            loading={loading}
            onSendToDify={sendToDify}
            onReset={resetTest}
          />
        );

      case 4:
        return (
          <DifyResultScreen
            zodiacSign={zodiacSign}
            mbtiType={mbtiType}
            gender={gender}
            difyResponse={difyResponse}
            onReset={resetTest}
            loading={loading}
          />
        );

      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 py-6 flex flex-col justify-center">
      {renderStep()}
    </div>
  );
};

export default MBTITest;