import React from 'react';
import {
  Box,
  Button,
  Container,
  Typography,
  Paper,
  Divider,
  CircularProgress,
  Grid,
  Card,
  CardContent
} from '@mui/material';

const ResultScreen = ({
  zodiacSign,
  mbtiType,
  typeDescription,
  scores,
  loading,
  onSendToDify,
  onReset
}) => {
  // MBTI
  const calculateDimension = (dim1, dim2) => {
    const max = Math.max(scores[dim1], scores[dim2]);
    const percentage = Math.round((max / (scores[dim1] + scores[dim2])) * 100);
    return {
      dominant: scores[dim1] > scores[dim2] ? dim1 : dim2,
      percentage: isNaN(percentage) ? 50 : percentage
    };
  };

  const dimensions = [
    calculateDimension('E', 'I'),
    calculateDimension('S', 'N'),
    calculateDimension('T', 'F'),
    calculateDimension('J', 'P')
  ];

  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4, textAlign: 'center' }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mb: 4 }}>
          <img
            src={process.env.PUBLIC_URL + "/toramaro-header.png"}
            alt="MBTI"
            style={{
              width: '180px',
              height: '180px',
              objectFit: 'contain',
              marginBottom: '1rem',
              display: 'block'
            }}
          />
          <Typography variant="h4" component="h1" gutterBottom>
            
          </Typography>
        </Box>

        <Paper elevation={3} sx={{ p: 4, mb: 4 }}>
          <Typography variant="h5" gutterBottom>
            : <strong>{mbtiType}</strong>
          </Typography>
          <Typography variant="subtitle1" gutterBottom>
            : {zodiacSign}
          </Typography>
          <Divider sx={{ my: 2 }} />
          <Typography variant="body1" paragraph>
            {typeDescription}
          </Typography>

          <Grid container spacing={2} sx={{ mt: 3 }}>
            {dimensions.map((dim, index) => {
              const labels = {
                0: { left: 'E', right: 'I' },
                1: { left: 'S', right: 'N' },
                2: { left: 'T', right: 'F' },
                3: { left: 'J', right: 'P' },
              };

              const isLeft = dim.dominant === 'E' || dim.dominant === 'S' || dim.dominant === 'T' || dim.dominant === 'J';
              const percentage = isLeft ? dim.percentage : 100 - dim.percentage;

              return (
                <Grid item xs={12} key={index}>
                  <Card variant="outlined">
                    <CardContent>
                      <Grid container alignItems="center">
                        <Grid item xs={3}>
                          <Typography variant="body2" color={isLeft ? 'primary' : 'text.secondary'}>
                            {labels[index].left}
                          </Typography>
                        </Grid>
                        <Grid item xs={6}>
                          <Box sx={{ position: 'relative', height: 10, bgcolor: 'grey.200', borderRadius: 5 }}>
                            <Box
                              sx={{
                                position: 'absolute',
                                left: 0,
                                top: 0,
                                height: '100%',
                                width: `${percentage}%`,
                                bgcolor: isLeft ? 'primary.main' : 'secondary.main',
                                borderRadius: 5
                              }}
                            />
                          </Box>
                        </Grid>
                        <Grid item xs={3} sx={{ textAlign: 'right' }}>
                          <Typography variant="body2" color={!isLeft ? 'secondary' : 'text.secondary'}>
                            {labels[index].right}
                          </Typography>
                        </Grid>
                      </Grid>
                    </CardContent>
                  </Card>
                </Grid>
              );
            })}
          </Grid>
        </Paper>

        <Box sx={{ mt: 4 }}>
          <Typography variant="h6" gutterBottom>
            
          </Typography>
          <Typography variant="body2" color="text.secondary" paragraph>
            MBTI
          </Typography>

          <Button
            variant="contained"
            color="primary"
            onClick={onSendToDify}
            disabled={loading}
            sx={{ mr: 2 }}
          >
            {loading ? (
              <>
                <CircularProgress size={24} color="inherit" sx={{ mr: 1 }} />
                ...
              </>
            ) : (
              ''
            )}
          </Button>

          <Button
            variant="outlined"
            onClick={onReset}
            disabled={loading}
          >
            
          </Button>
        </Box>
      </Box>
    </Container>
  );
};

export default ResultScreen;